import React from "react";
import { ChatItem } from "./ChatItems/ChatItem";
import "./chatlist.css";
import Chatdata from "../Chattabdata";

export const ChatList = () => {
  return (
    <>
      {Chatdata.map((val) => {
        return (
            <ChatItem
              userimg={val.userimg}
              chatuser={val.chatuser}
              userchatleft={val.userchatleft}
              seenbtn={val.seenbtn}
              userchattime={val.userchattime}
              userchatmode={val.userchatmode}
            />
        );
      })}
    </>
  );
};
