import React from "react";
import "./chattab.css";





export const ChatItem = (props) => { 

  return (
    <div className="chatTab">
      <div className="ImgAndName">
        <div className="chatimg">
          <img src={props.userimg}  alt="" />
        </div>
        <div className="nameDetails">
          <h4>{props.chatuser} </h4>
          <p><i class={props.seenbtn}></i>{props.userchatleft} </p>
        </div>
      </div>
      <div className="timeandswitch">
        <div className="chatTime">
          <h5>{props.userchattime} </h5>
        </div>
        <div className="mode"><i class= {props.userchatmode}></i>
       
        </div>
      </div>
    </div>
  );
};
