import React, { useState } from "react";
import "./Navbar.css";

export const Navleft = () => {
  const [profileisActive, profilesetIsActive] = useState(false);
  return (
    <>
      <div className="navbar">
        <div className="logo ">
          {/* <i class="uil uil-whatsapp"></i> */}
          <img
            src="gaurav soni.jpeg"
            alt=""
            onClick={(e) => profilesetIsActive(!profileisActive)}
          />
        </div>

        <div className="navlist">
          <div className="story">
            <i class="fas fa-circle-notch"></i>
          </div>
          <div className="plusChat">
            <i class="uil uil-comment-alt-message"></i>
          </div>
          <div className="menu">
            <i class="uil uil-ellipsis-v"></i>

            {profileisActive && (
              <div className="myprofile">
                <div className="profiletextarea">
                  <i class="uil uil-arrow-left"></i>
                  <h5>Profile</h5>
                </div>
                <div className="profileimg">
                  <img src="./gaurav soni.jpeg" alt="" />
                </div>

                <div className="namesection">
                  <p>Your Name</p>
                  <div className="myusername">
                    <p>Gaurav Soni</p>
                    <i class="uil uil-pen"></i>
                  </div>
                </div>
                <div className="normaltext">
                  <p>
                    This is not your username or pin. This name will be visible
                    to your WhatsApp contacts.
                  </p>
                </div>

                <div className="biosection">
                  <p>About</p>
                  <div className="myusername">
                    <p>Maximum Efforts </p>
                    <i class="uil uil-pen"></i>
                  </div>
                </div>
                <div className="blanksection"></div>
              </div>
            )}
          </div>
        </div>
      </div>

      <div className="leftnavsearch">
        <div className="searchtab">
          <input type="text" placeholder="Search or start a new chat" />
        </div>
      </div>
    </>
  );
};
