import React, { useState } from "react";
import "./userfooter.css";
import "./userchatting.css";

export const ActiveUserFooter = () => {
  const [name, setName] = useState("");
  const [texthere, settexthere] = useState([]);

  const typeevent = (event) => {
    setName(event.target.value);
  };

  const onSend = () => {
    settexthere((oldItems)=>{
return[...oldItems, name ]

    });

    setName("");
  };



  return (
    <>
      <div className=" chattingplayground">
        <div className="messageentered">
          <ul>
            {/* <li>{texthere}</li> */}

            {texthere.map( (ItemsVal)=> {
              return(
                <li>{ItemsVal } <i class="uil uil-check"></i> </li>
              )
            })}

          </ul>
        </div>
      </div>


      {/* footer section ------------------------------- */}

      <div className="chatfooterarea">
        <div className="emogie">
          <i class="uil uil-smile"></i>
        </div>
        <div className="link">
          <i class="uil uil-paperclip"></i>
        </div>



        <div className="chattextinput">
          <input
            type="text"
            placeholder="Type a message"
            onChange={typeevent}
            value={name}
          />
        </div>



        <div className="sendchattext">
          <i class="uil uil-message" onClick={onSend}></i>
        </div>
      </div>
    </>
  );
};
